@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}
.login-form {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  margin: auto;
}
.container-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.home,
.cources,
.timetable {
  display: flex;
  height: 80vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
.progressbar-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.progress {
  width: 80%;
}
.footer {
  background: #343a40;
  margin-top: 20px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer-text {
  color: #ffff;
  font-size: 1rem;
}

.navbar {
  background-color: #343a40;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.dropdown {
  list-style: none;
  margin-right: 50px;
}
.dropdown a {
  color: #ffff;
  font-size: 18px;
}
.dropdown a:hover {
  color: #ffff;
}
.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}
.nav-menu {
  background-color: #343a40;
  width: 250px;
  height: 95vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}
.nav-menu.active {
  left: 0;
  transition: 350ms;
}
.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}
.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 20px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}
.nav-text a:hover {
  text-decoration: none;
}
.nav-menu-items {
  width: 100%;
}
.navbar-toggle {
  background-color: #343a40;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
span {
  margin-left: 16px;
}

